
import React, { useEffect, useState } from 'react'
import RandomRectangle from 'atoms/RandomRectangle'


const D = 120;

export default function RectangleBundle(props) {

    const [poss, setPoss] = useState([]);

    const pushOrg = (x) => {
        return (Math.abs(x) + 1) * (x > 0 ? 1 : -1);
    }

    const avoidNrandom = () => {
        let v = [Math.random() * D - 0.5 * D, pushOrg(Math.random() * D - 0.5 * D), pushOrg(Math.random() * D - 0.5 * D)];

        return v
    };
    const init = async () => {

        setPoss([...Array.from({ length: 400 }, (v, idx) => { return avoidNrandom() })]);
        //console.log("POSSES", poss);
    };

    useEffect(() => {
        init();
    }, []);

    return (
        <>
            {poss.map(x => (<RandomRectangle position={x} />))}
        </>
    )
}